
//import './App.css';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import Content from './Components.jsx/Content/Content';
import Header from './Components.jsx/Header';
import SmoothScrollBar from './Components.jsx/SmoothScrollBar'
import ColorItems from './Components.jsx/BackgroundColors/ColorItems';
import AOS from 'aos'
//import 'aos/dist/aos.css'
import { useEffect, useState } from 'react';
import Footer from './Components.jsx/Footer';
import About from './Components.jsx/Content/About';
import GetInTouch from './Components.jsx/Content/GetInTouch';
import Portfolio from './Components.jsx/Content/Portfolio';

function App() {

  const colors = ['#2d3436', '#4834d4', '#bezed0', '#f9ca24', '#6ab04c', '#30336b'];

  const [state, setState]=useState(false);


  // useEffect(()=>{
  //   AOS.init({duration:2000})
  // },[])

  const scroll =()=>{
    return null
  }
  useEffect(()=>{
    const currentThemeColor = localStorage.getItem('color')
    setTheme(currentThemeColor)
  },[])

  const setTheme=(color)=>{
    document.documentElement.style.setProperty('--bg-color', color)
  }

  const setColor=(event)=>{
    const currentColor = event.target.style.getPropertyValue('--bg-color');
    setTheme(currentColor)
    localStorage.setItem('color', currentColor)
  }

  return (
    <div className="bg-black" data-aos="">
    {/* <div className={`color-switcher text-center ${state && 'color-switcher-open' } `}>
      <button onClick={()=>setState(prev=>!prev)}><i className="fa fa-gear"></i></button>
    <div className="heading"> Select Color</div>
      <div  className="color-list">
            {colors.map((color,index)=> <ColorItems key={index} setColor={setColor} color={color} />)}
     </div>
    </div> */}
    <Header/>
    <BrowserRouter>
    <Routes>
    <Route path='/home'/>
    <Route path='/about' element={<About/>}/>
    <Route path='/portfolio' element={<Portfolio/>}/>
    <Route path='/contact' element={<GetInTouch/>}/>
   </Routes>
   </BrowserRouter>
   <Content/>
   <About/>
   <Portfolio/>
   <GetInTouch/>
   <Footer/> 
    </div>
  );
}

export default App;
